.arrow-down {
    position: absolute;
    left: 50%;
    margin-left: -73px;
    bottom: -937px;
    width: 0;
    height: 0;
    border-left: 84px solid transparent;
    border-right: 84px solid transparent;
    border-top: 84px solid #cdcdcd;
    /* animation: arrow-down 1s infinite; */
}

.arrow-down-txt {
    font-size: 55px;
    width: 400px;
    position: absolute;
    left: 50%;
    margin-left: -122px;
    bottom: -760px;
    height: 0;
    color: #cdcdcd;
    /* animation: arrow-down 1s infinite; */
}

@keyframes arrow-down {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(10px);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes slide-out {
    from {
        transform: translateX(0);
        opacity: 1;
    }
    to {
        transform: translateX(100%);
        opacity: 0;
    }
}

.project-card {
    /*min-width: 450px !important;*/
    cursor: pointer;
    position: relative;
    display: inline-block;
    background-repeat: no-repeat;
    background-size: cover;
}

.project-card img {
    /*min-width: 400px !important;*/
    /*display: block;*/
    /*transition: filter 0.3s ease-in-out;*/
}

.project-card:hover img {
    filter: brightness(20%);
}

.project-card:hover .caption {
    cursor: pointer;
    opacity: 1;
}

.caption {
    cursor: pointer;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 10px;
    color: white;
    font-size: 18px;
    text-align: center;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.caption::before {
    cursor: pointer;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.5;
}

.hoverable_caption {
    cursor: pointer;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    justify-items: center;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    padding: 10px;
    color: white;
    font-size: 18px;
    text-align: center;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.hoverable_caption::before {
    cursor: pointer;
    position: absolute;
    top: -20%;
    left: 40%;
    /* bottom: 50%; */
    /* content: ""; */
    /* display: flex; */
    align-items: center;
    align-content: center;
    justify-content: center;
    /* justify-items: center; */
    width: 20%;
    padding: 0;
    margin: 0;
    border-bottom: 2px solid #cf2e2e;
    animation: fadeIn 2s ease-in-out forwards;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        scale: 0;
    }
    to {
        opacity: 1;
        scale: 1;
    }
}

.modal-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 10;
}

.modal {
    position: fixed;
